<template>
  <div class="login-view">
    <el-row justify="center">
      <el-form class="border" label-width="auto" size="large">
        <el-form-item>
          <div style="text-align: center; width: 100%">
            江苏省中医院自动挂号
          </div>
        </el-form-item>
        <el-form-item>
          <el-input v-model="username" placeholder="请输入用户名"></el-input>
        </el-form-item>
        <el-form-item>
          <el-input
            v-model="password"
            placeholder="请输入密码"
            type="password"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-input
            v-model="verification_code"
            placeholder="请输入验证码"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <img :src="verification_code_img" alt="加载失败" />
        </el-form-item>

        <el-row justify="center">
          <el-button type="primary" @click="login">登录</el-button>
          <el-button type="primary" @click="reset">重置</el-button>
        </el-row>
      </el-form>
    </el-row>
  </div>
</template>

<script>
import { ElMessage } from "element-plus";
import { Store } from "@/stores";
import { API } from "@/utils/API";
import { Md5 } from "ts-md5/dist/md5";

const store = Store();
export default {
  name: "LoginView",
  data() {
    return {
      username: "",
      password: "",
      required: true,
      verification_code_img: "",
      uniqueId: "",
      verification_code: "",
    };
  },
  methods: {
    login() {
      const md5 = new Md5();
      md5.appendStr(this.password);
      const payload = {
        tenantId: "hcn.jsszyy",
        pwd: md5.end(),
        forAccessToken: true,
        rid: "patient",
        verifyCode: this.verification_code,
        uniqueId: this.uniqueId,
        loginName: this.username,
      };
      this.axios
        .post(API.login, payload)
        .then((res) => {
          if (res.data.code === 200) {
            ElMessage.success(`用户：${res.data.body.userName} 登录成功`);
            store.accessToken = res.data.properties.accessToken;
            this.get_he_ren_token();
          } else {
            ElMessage.error(res.data.msg);
          }
        })
        .catch((reason) => {
          ElMessage.error(reason);
          store.token = "tmp";
          this.$router.push("/hospitalzone");
        });
    },
    reset() {
      this.username = "";
      this.password = "";
      this.verification_code = "";
    },
    refresh_verification_code() {
      const headers = {
        "Content-Type": "application/json",
        "X-Service-Id": "cas-wechat.smsService",
        "X-Service-Method": "getVerifyCodeInfo",
      };
      const payload = [{}];
      this.axios
        .post(API.jsonRequest, payload, { headers: headers })
        .then((res) => {
          const body = JSON.parse(res.data.body);
          this.verification_code_img =
            "data:image/png;base64," + body.verifyCode;
          this.uniqueId = body.uniqueId;
        })
        .catch((err) => {
          console.log("获取验证码失败！");
          console.log(err);
        });
    },
    get_he_ren_token() {
      const headers = {
        "X-Service-Method": "getHeRenToken",
        "X-Access-Token": store.accessToken,
        "X-Service-Id": "cas-wechat.heRenService",
        "Content-Type": "application/json",
      };
      const payload = [{ openId: "" }];
      this.axios
        .post(API.jsonRequest, payload, { headers })
        .then((res) => {
          if (res.data.code === 200) {
            store.token = res.data.body;
            localStorage.setItem(
              "jshtcm",
              JSON.stringify({
                token: store.token,
                accessToken: store.accessToken,
              })
            );
            this.$router.push("/hospitalzone");
            console.log("登陆成功，跳转页面");
          } else {
            ElMessage.error(res.data.msg);
          }
        })
        .catch((reason) => {
          ElMessage.error(reason);
          console.log(reason);
        });
    },
  },
  mounted() {
    this.refresh_verification_code();
  },
};
</script>

